.container {
  max-width: 1100px;
  margin: 0 auto;
  overflow: auto;
  padding: 0 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wrapper {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 600px;
  width: 100%;
  overflow: hidden;
  position: relative;
}
.contentContainer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.nameTag {
  margin-top: 40px;
  font-family: "Open Sans", sans-serif;
  color: #ba5d2c;
  font-weight: 600;
}
.pageTitle {
  font-family: "Open Sans", sans-serif;
  font-size: 40px;
  margin-top: 40px;
}
.jobTitle {
  margin-top: 5px;
  font-family: "Open Sans", sans-serif;
}
.pageInfo {
  width: 65%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
}
.pageInfo p {
  text-align: center;
  font-family: "Noto Sans", sans-serif;
  font-weight: 400;
  color: #617da4;
}
.imgContainer img {
  width: 150px;
  height: 150px;
  border-radius: 80px;
  box-shadow: rgba(100, 100, 111, 0.2) 10px 7px 29px 10px;
  border: 4px rgb(124, 124, 124) solid;
}
.iconclass {
  display: flex;
  justify-content: center;
  position: relative;
  bottom: 200px;
  cursor: pointer;
}
.iconL {
  position: relative;
  right: 300px;
  background-color: black;
  color: white;
  font-size: 25px;
  border-radius: 5px;
}
.iconR {
  position: relative;
  left: 300px;
  background-color: black;
  color: white;
  font-size: 25px;
  border-radius: 5px;
  z-index: 10;
}
.quotes {
  margin-top: 30px;
  font-size: 40px;

  color: #ba5d2c;
}

.activeSlide {
  opacity: 1;
  transition: all 0.3s linear;
  transform: translateX(0);
}
.lastSlide {
  transform: translateX(-100%);
}
.nextSlide {
  transform: translateX(100%);
}

/* tablet*/
@media (max-width: 768px) {
  .wrapper {
    width: 120%;
  }
  .iconR {
    left: 250px;
  }
  .iconL {
    right: 250px;
  }
  .pageInfo {
    width: 80%;
    margin-top: 20px;
    font-size: 15px;
  }
}
/* Mobile*/
@media (max-width: 500px) {
  .pageInfo {
    width: 100%;
    margin-top: 20px;
    font-size: 20px;
  }
  .iconclass {
    top: 200px;
  }
  .iconR {
    left: 70px;
  }
  .iconL {
    right: 70px;
  }
  .iconclass {
    top: 200px;
  }
}

/* Mobile*/
@media (max-width: 400px) and (min-width: 320px) {
  .wrapper {
    width: 120%;
  }
  .contentContainer {
    width: 100%;
  }
  .pageInfo {
    width: 100%;
    margin-top: 20px;
    font-size: 15px;
  }
  .iconR {
    left: 70px;
  }
  .iconL {
    right: 70px;
  }

  .iconclass {
    top: 200px;
  }
  .pageTitle {
    font-family: "Open Sans", sans-serif;
    font-size: 30px;
    text-align: center;
  }
}
